%back-button-gradient {
  display: block;
  font-size: 18px;
  color: #fff!important;
  text-align: center;
  background: linear-gradient(90deg, #FF5308 0%, #F30874 50%, #C20059 100%);
  background-size: 200%;
  transition: all .2s ease;
  &:hover{
    background-position: 100% 0;
  }
}
@import 'vars';


html{
  height: 100%;
  overflow-x: hidden;
}
body {
  font-family: $family-stack;
  color: $color-primary;
  //background: #F8F6E6;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  background: #F8F6E6 url("/img/head-back.jpg") no-repeat -83px 59px;
  //background-position: -83px 70px;
  &.fixed{
    overflow-y: hidden;
  }
}
.container{
  margin: 0 8px;
}
#overlay{
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(76, 23, 0, 0.15);
  z-index: 900;
}
header{
  position: relative;
  padding: 44px 0 0;
  &:before{
    content: "";
    display: block;
    position: fixed;
    top: 0;
    width: 100%;
    height: 44px;
    z-index: 890;
    background: #fff;
    transition: all .15s;
  }
  &.fixed:before{
    box-shadow: 0 2px 16px rgba(174, 109, 33, 0.25);
  }
  .desc{
    display: none;
  }
}
#logo{
  display: block;
  position: fixed;
  height: 44px;
  top: 0;
  left: 8px;
  padding: 4px 8px;
  z-index: 891;
  &:hover{
    opacity: .7;
  }
  img{
    width: 126px;
    height: 35px;
  }
}
#contacts{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 22px 8px 8px;
  font-size: 15px;
  div{
    padding: 4px 0;
    white-space: nowrap;
  }
  i{
    position: relative;
    top: 4px;
    font-size: 23px;
    line-height: 21px;
    color: #996036;
  }
  span{
    span{
      font-size: 18px;
    }
  }
}
.menu-input{
  display:none;
}
.menu-button{
  position: fixed;
  top: 0;
  right: 0;
  width: 58px;
  height: 44px;
  background: #fff;
  cursor: pointer;
  z-index: 920;
  & span, span:after, span:before {
    content: "";
    display: block;
    background: $color-active;
    border-radius: 1px;
    width: 21px;
    height: 2px;
    position: relative;
    transition: all .15s ease .1s;
  }
  span{
    margin: 0 auto;
    top: 21px;
  }
  span:before{
    top: -8px;
  }
  span:after{
    top: 6px;
    left: 7px;
    width: 14px;
  }
  &:hover span, &:hover span:after, &:hover span:before{
    background: $color-hover;
  }
  .menu-input:checked + &{
    background: transparent;
    transition: all .1s .1s;
    span {
      background-color: transparent;
    }
    span:before{
      top: 0;
      transform: rotate(135deg);
    }
    span:after{
      top: -2px;
      left: 0;
      width: 21px;
      transform: rotate(-135deg);
    }
  }
}

nav{
  position: fixed;
  top: 0;
  bottom: 0;
  right: -275px;
  width: 275px;
  z-index: 910;
  transition: right .2s ease;

  ul {
    padding: 15px 0 0;
  }
  a {
    display: block;
    font-size: 18px;
    padding: 9px 25px;
    &:hover{
      background: $color-hover-back-2;
    }
    &#more{
      display: none;
    }
  }
  .fav span{
    display: inline-block;
    position: relative;
    top: -9px;
    left: -6px;
    font-size: 13px;
    line-height: 15px;
    padding: 1px 4px 0;
    color: #fff;
    border-radius: 9px;
    background: $color-hover;
  }
  #menu-button:checked ~ &{
    right: 0;
  }
}
#wrap-drop {
  display: none;
}

.button-send{
  @extend %back-button-gradient;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  line-height: 43px;
  z-index: 1000;
}


aside{
  //margin: 0 8px;
}
/*Поле поиска*/
#search{
  display: flex;
  margin: 16px 8px;
  i{
    width: 30px;
    font-size: 20px;
    line-height: 36px;
    text-align: right;
  }
  input{
    display: block;
    font-size: 16px;
    line-height: 36px;
    padding: 0 16px;
    color: $color-primary;
    background: none;
    &::placeholder{
      color: $color-active;
    }
  }
}

/*Фильтр*/
#filter{
  position: relative;
  margin: 16px 8px;
  h4{
    font-size: 16px;
    line-height: 36px;
    font-weight: normal;
    padding: 0 16px;
    cursor: pointer;
  }
}
#opts{
  display: none;
  border-top: 1px solid $color-hover-back;
  //padding: 8px 0;
  > div{
    display: flex;
    flex-wrap: wrap;
    padding: 24px 16px;
  }
  .sections{
    padding: 24px 12px 16px;
    input{
      display: none;
      /*&:checked + label:hover{
        color: $color-active;
        background: $color-hover-back;
      }*/
      &:checked + label{
        color: #fff;
        background: $color-active;
      }
    }
    label{
      float: left;
      margin: 0 4px 8px;
      line-height: 32px;
      padding: 0 16px;
      color: $color-active;
      background: $color-hover-back;
      cursor: pointer;
      transition: all .15s;
      /*&:hover{
        color: #fff;
        background: $color-active;
      }*/
    }
  }
  .years{
    border-top: 1px solid $color-hover-back;
  }
  .clear{
    visibility: hidden;
    opacity: 0;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 16px;
    line-height: 36px;
    color: $color-active-filter;
    padding: 0 16px;
    transition: all .15s;
    &.active{
      visibility: visible;
      opacity: 1;
    }
    &:hover{
      color: $color-hover;
    }
  }
}



/*Сетка Masonry*/
#masonry{
  width: 100%!important;
  > div{
    width: 100%;
    float: left;
    padding: 0 8px 16px;
  }
  .item{
    padding: 16px;
    transition: all .15s;
    &:hover{
      box-shadow: 0 2px 16px rgba(174,109,33,.2);
    }
  }
}


footer {
  border-top: 1px solid $color-border;
  .container{
    padding: 30px 8px;
  }
}

