@media screen and (min-width: 1200px) {
  body{
    background-position: right 7.6% top 54px;
  }
  .button-send {
    position: static;
    width: 250px;
    font-size: 18px;
    line-height: 50px;
    margin-right: 8px;
    z-index: auto;
    &:hover{
      transform: translateY(-2px);
      box-shadow: 0 2px 16px rgba(174,109,33,.25);
    }
  }
  header{
    .desc{
      margin-right: 150px;
    }
  }
  nav{
    ul.menu{
      margin: 0 auto;
      li.more{
        display: block;
      }
      a{
        padding: 5px 16px;
      }
    }
    .fav{
      position: relative;
      span{
        position: absolute;
        top: -1px;
        right: 0;
        left: auto;
      }
    }
    a#more{
      display: none;
    }
  }
  #wrap-drop {
    display: none!important;
  }
}