$family-stack: 'Istok Web', sans-serif;
$color-primary: #4C1700;
$color-active: #996036;
$color-active-filter: #D4A578;
$color-border: #EDCFAB;
$color-hover-back: #F3EBD5;
$color-hover-back-2: #F8F6E8;
$color-hover: #F24700;


@font-face {
  font-family: 'Icons';
  src:
          url('/fonts/Icons.ttf?7wl8vw') format('truetype'),
          url('/fonts/Icons.woff?7wl8vw') format('woff'),
          url('/fonts/Icons.svg?7wl8vw#Icons') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}


.icon-telegram:before {
    content: "\e900";
}
.icon-viber:before {
    content: "\e901";
}
.icon-whatsapp:before {
    content: "\e902";
}
.icon-more:before {
  content: "\e903";
}
*{
  font-family: $family-stack;
  font-size: 16px;
  line-height: 22px;
}
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  background: none;
}
button {
  cursor: pointer;
}
i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'Icons' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
ul {
  list-style: none;
}
a{
  color: $color-primary;
  text-decoration: none;
  transition: all .15s;
  &:hover{
    color: $color-hover;
  }
}
.white {
  background: #fff;
}
.border {
  border: 1px solid $color-border;
}
.round {
  border-radius: 3px;
}
[data-hide]:before {
  content: attr(data-hide);
  padding: 0 5px 0 0;
}