@media screen and (min-width: 1340px) {
  body{
    background-position: right 65px top 54px;
  }
  .container{
    width: 1330px;
    margin: 0 auto;
  }
  header{
    .container{
      padding: 26px 0 13px;
    }
    .desc{
      display: flex;
      align-items: center;
      width: auto;
      margin-right: 140px;
      border: none;
      h4{
        width: 130px;
        font-size: 16px;
        padding: 0 11px;
        text-align: right;
      }
      p{
        width: 227px;
        padding: 0 8px 0 11px;
        border-left: 1px solid #996036;
      }
    }
  }
  nav{
    ul.menu{
      width: 1318px;
      padding: 0;
    }
  }
}
@media screen and (min-width: 1410px) {
  body{
    background-position: right 11.3% top 54px;
  }
}
@media screen and (min-width: 1506px) {
  body{
    background-position: right 14.6% top 54px;
  }
}
@media screen and (min-width: 1570px) {
  body{
    background-position: right 16.6% top 54px;
  }
}
@media screen and (min-width: 1890px) {
  body{
    background-position: right 23.7% top 54px;
  }
}