@media screen and (min-width: 640px) {
  body{
    background-position: 100px 59px;
  }
  .menu-button {
    display: none;
  }
  nav{
    width: 100%;
    right: 0;
    bottom: auto;
    ul.menu{
      display: flex;
      justify-content: space-between;
      padding: 0 22px;
      li{
        &.more{
          display: none;
        }
      }
      a{
        font-size: 16px;
        padding: 5px 8px;
        margin: 5px 2px;
        white-space: nowrap;
        border-radius: 3px;
      }
    }
    a#more{
      display: block;
      width: 58px;
      position: relative;
      padding: 5px 18px 5px 8px;
      margin-right: 5px;
      &.active{
        background: #f8f6e8;
      }
      .icon-more{
        font-size: 22px;
        line-height: 0;
        position: relative;
        top: 4px;
        left: -1px;
      }
    }
  }

  #wrap-drop{
    position: absolute;
    width: 307px;
    top: 42px;
    right: 0;
    padding: 0 16px 16px;
    z-index: 920;
    overflow-y: hidden;
  }
  #more-drop {
    padding: 10px 0;
    background: #fff;
    border-radius: 0 0 3px 3px;
    box-shadow: 0 2px 16px rgba(174, 109, 33, 0.25);
    a{
      display: block;
      font-size: 16px;
      padding: 7px 25px;
      margin: 0;
    }
  }
  header{
    width: 100%;
    &:before{
      height: 42px;
    }
    .container{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 22px 0 8px;
    }
  }
  #logo{
    height: 49px;
    left: 16px;
    position: relative;
    background: url(/img/logo-back.svg) no-repeat 8px 4px;
    background-size: 148px 41px;
    &:hover{
      transform: scale(1.05);
      opacity: 1;
    }
    img{
      width: 148px;
      height: 41px;
    }
  }
  #contacts {
    padding: 0 24px 0;
  }
  /*Сетка Masonry*/
  #masonry{
    > div{
      width: 50%;
    }
  }


}