@media screen and (min-width: 768px) {
  body {
    background-position: 86px 68px;
  }
  nav{
    ul.menu{
      margin-right: 235px;
    }
  }
  #wrap-drop{
    right: 230px;
  }
  .button-send{
    width: 220px;
    bottom: auto;
    top: 5px;
    left: auto;
    right: 16px;
    font-size: 16px;
    line-height: 32px;
    border-radius: 3px;
  }
  header{
    .desc{
      display: block;
      width: 227px;
      font-size: 15px;
      padding: 0 8px 0 11px;
      border-left: 1px solid $color-active;
      h4{
        font-size: 15px;
        font-weight: normal;
        color: $color-active;
      }
      p{
        line-height: 125%;
      }
    }
  }
  #contacts{
    margin-left: auto;
    padding: 0 12px;
  }
  #masonry{
    > div{
      width: 33.33%;
    }
  }
}